html, body {
  height: 100%;
  margin: 0;
  background-color: #f5f5f5;
  background-image: url('https://aipict.com/wp-content/uploads/2023/09/snowy-mountains01.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
}

#root {
  flex: 1;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  width: 100%;
  padding-top: 110px;
  box-sizing: border-box;
}

.title-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 10px 0;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  z-index: 1000;
}

.title {
  font-size: 1.5rem;
  color: #333;
  font-weight: bold;
  margin: 0 10px;
  white-space: nowrap;
}

.game-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh; /* Ensure full viewport height */
  position: relative;
  overflow: hidden; /* Allows clipping of oversized elements, but controls it */
  background-color: transparent;
  box-sizing: border-box;
}

.snowboarder, .bigfoot {
  position: absolute;
  width: 100px; /* Use consistent base width */
  height: auto; /* Ensure aspect ratio */
  top: 50%; /* Make sure they vertically align */
  transform: translateY(-50%); /* Center vertically */
  z-index: 1001; /* Just ensuring it's above relative things */
  background-size: contain;
  background-repeat: no-repeat;
}

.score {
  position: absolute;
  top: 0;
  font-size: 3rem;
  font-weight: bold;
  color: #333;
  text-align: center;
  width: 100%;
  margin-top: 80px;
}

input[type='file'],
.prompt-input {
  margin: 15px 0;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 1rem;
  width: 90%;
  max-width: 350px;
}

.upload-button {
  background-color: #007bff;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 1rem;
}

.upload-button:hover {
  background-color: #0056b3;
}

.response-box {
  margin-top: 15px;
  background-color: #fff;
  color: #333;
  padding: 12px;
  border-radius: 8px;
  width: 90%;
  max-width: 350px;
  min-height: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid #ddd;
  overflow-wrap: break-word;
  white-space: pre-wrap;
  text-align: left;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.response-box.scrollable {
  max-height: 150px;
  overflow-y: auto;
}

.loading-gif {
  width: 20vw;
  height: 20vw;
}

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
  width: 100%;
  padding-top: 10vh;
  box-sizing: border-box;
  text-align: center;
}

.home-title {
  font-size: 2.5rem;
  color: #333;
  margin: 5px 0;
}

.home-description {
  font-size: 1.5rem;
  color: #666;
  margin-top: 8px;
}

.spin-off {
  animation: spinOff 1.33s ease-in-out forwards;
}

@keyframes spinOff {
  0% { transform: none; opacity: 1; }
  100% { transform: translate(-250px, -400px) rotate(-720deg); opacity: 0; }
}

.loss-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1100; /* High to display on top of other content */
  text-align: center;
  padding: 20px;
  border-radius: 8px;
}

.loss-message {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loss-text {
  font-size: 36px;
  color: rgb(102, 182, 232);
  margin-bottom: 10px;
  text-shadow: 4px 4px 4px #000000;
  font-weight: bold;
}

.loss-message img {
  max-width: 100%;
  height: auto;
}

@media (max-width: 768px) {
  .title {
    font-size: 1.2rem;
  }

  .score {
    font-size: 2.5rem;
  }

  .snowboarder, .bigfoot, .loading-gif {
    width: 18vw;
    height: 18vw;
  }

  .home-title {
    font-size: 2rem;
  }

  .home-description {
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .title-bar {
    flex-wrap: wrap;
    height: auto;
  }

  .title {
    font-size: 1rem;
    margin: 5px;
    text-align: center;
  }

  .score {
    margin-top: 85px;
    font-size: 2rem;
  }

  .snowboarder, .bigfoot, .loading-gif {
    width: 25vw;
    height: 25vw;
  }

  .home-title {
    font-size: 1.5rem;
  }

  .home-description {
    font-size: 1rem;
  }

  .loss-text {
    font-size: 24px;
  }

  .loss-popup {
    padding: 20px;
  }
}